import React, { Component } from 'react';
import OrdererName from "./OrdererName/OrdererName.js";
import CoffeeSelect from "./CoffeeSelect/CoffeeSelect.js";
import { Button, Form, FormGroup } from 'reactstrap';
import './Main.css';


class Main extends Component {
    state = {
        ordererName: "",
        orderMenu: ""
    }

 _onChangeName = (e) => {
     console.log(e.target.value);
  this.setState({
      ordererName : e.target.value,
  });
}
  _onChangeMenu = (e) => {
      console.log(e.target.value);
      this.setState({
          orderMenu : e.target.value,
      });
  }

  _onClick = () => {
    if (this.state.ordererName === ""){
      console.log("name is Required");
      return;
    }
    if (this.state.orderMenu === ""){
        console.log("Menu is Required");
        return;
    }
    console.log(this.state.ordererName + ", " + this.state.orderMenu);
}

  // xs, sm, md, lg : 화면 크기에 따라 사이즈를 지정해 줄 수 있음
  // 12를 가지고 나눠서 먹는듯
  render() {
    return (
      <Form>
        <FormGroup align="center">
            <h2>커피 주문</h2>
        </FormGroup>
        <OrdererName changedName={this._onChangeName}/>
        <CoffeeSelect changedMenu={this._onChangeMenu}/>
        <FormGroup align="center">
            <Button  color="primary" onClick={this._onClick}>주문하기</Button>
        </FormGroup>
      </Form>
    );
  }
}

export default Main;



