import React from 'react';
import { Col, FormGroup, Label, Input } from 'reactstrap';
import './OrdererName.css';


function OrdererName ({changedName}) {
  // xs, sm, md, lg : 화면 크기에 따라 사이즈를 지정해 줄 수 있음

    return (
      <FormGroup row>
      <Col xs={1}/>
      <Col xs={3}>
        <Label for="nameSelect">이름 :</Label>
      </Col>
      <Col xs={7}>
         <Input type="select" name="select" id="nameSelect" onChange={changedName}>
           <option value="">이름 선택</option>
           <option>이재윤</option>
           <option>우정곤</option>
         </Input>
       </Col>
      </FormGroup>
    );
}

export default OrdererName;



